import uniStatusBar from '../uni-status-bar/uni-status-bar.vue'
import uniIcon from '../uni-icon/uni-icon.vue'

export default {
	name: 'UniNavBar',
	components: {
		uniStatusBar,
		uniIcon
	},
	props: {

		backHeight: {
			type: String,
			default: ''
		},
		theme: {
			type: String,
			default: ''
		},
		title: {
			type: String,
			default: ''
		},
		leftText: {
			type: String,
			default: ''
		},
		rightText: {
			type: String,
			default: ''
		},
		leftIcon: {
			type: String,
			default: ''
		},
		rightIcon: {
			type: String,
			default: ''
		},
		fixed: {
			type: Boolean,
			default: false
		},
		color: {
			type: String,
			default: 'white'
		},

		statusBar: {
			type: Boolean,
			default: false
		},
		shadow: {
			type: Boolean,
			default: true
		},
		border: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			statusHeight: 0,
			statusline: 0,
			backColor: 'background-image: linear-gradient(to right top, #ffd46e, #ffc17b, #ffb18f, #ffa6a6, #ffa1bb, #fd9fc6, #f99ed2, #f29edf, #e79ce2, #dc9ae5, #cf98e8, #c297ea)',
			backStatus: 1
		}
	},


	mounted() {
		uni.$on('theme', function(data) {
			let theme = uni.getStorageSync('theme');
			this.backStatus = theme.backStatus
			this.backColor = theme.backColor;
			// console.log("改变" + this.backColor)
			// console.log(this.$refs.box)
		})
		let theme = uni.getStorageSync('theme');
		this.backStatus = theme.backStatus
		this.backColor = theme.backColor
		this.getTitleHeight()
	},
	methods: {
		onClickLeft() {
			this.$emit('click-left')
		},
		onClickRight() {
			this.$emit('click-right')
		},
		getTitleHeight() {
			let that = this;
			uni.getSystemInfo({
				success: res => {
					console.log(res.screenHeight)
					if (res.screenHeight < 740) {
						that.statusline = "90px"
						that.statusHeight = "60px"
						return false;
					}
					if (res.screenHeight < 950) {
						that.statusline = "100px"
						that.statusHeight = "70px"
						return false;
					}
					if (res.screenHeight > 1000) {
						that.statusline = "110px";
						that.statusHeight = "80px";
						return false;
					}
				}
			})
		}

	}
}
